import React from "react";
import { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  AnimatePresence,
  motion,
  useScroll,
  useTransform,
} from "framer-motion";

function HeaderContent() {
  const [toggle, setToggle] = useState(true);

  const toggler = () => {
    toggle ? setToggle(false) : setToggle(true);
  };

  function PageButton2({ href, children, ...props }) {
    const path = window.location.pathname;

    return (
      <a>
        <Link
          className={
            path === href
              ? "bg-gray-900 text-white block rounded-md px-3 py-2 text-5xl font-medium"
              : "text-gray-300 hover:bg-gray-600 hover:text-white block rounded-md px-3 py-2 text-5xl font-medium"
          }
          to={href}
        >
          {children}
        </Link>
      </a>
    );
  }

  const path = window.location.pathname;

  return (
    <div>
      <div class="mx-auto max-w-screen-2xl px-2 h-auto sm:px-6 lg:px-8 bg-opacity-10">
        <div class="relative flex items-center justify-between">
          <div class="absolute inset-y-0 right-0 flex items-center lg:hidden">
            {/* Mobile menu button*/}
            <button
              type="button"
              onClick={toggler}
              class={
                toggle
                  ? "inline-flex items-center justify-center rounded-md p-2 text-gray-400 "
                  : "inline items-center justify-center rounded-md p-2 text-white"
              }
              aria-controls="mobile-menu"
              aria-expanded="false"
            >
              <span class="sr-only">Open main menu</span>
              {/*
                    Icon when menu is closed.
        
                    Menu open: "hidden", Menu closed: "block"
                  */}
              <svg
                class="block h-9 w-9"
                fill="none"
                viewBox="0 0 24 24"
                stroke-width="1.5"
                stroke="currentColor"
                aria-hidden="true"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  d="M3.75 6.75h16.5M3.75 12h16.5m-16.5 5.25h16.5"
                />
              </svg>
              {/*
                    Icon when menu is open.
        
                    Menu open: "block", Menu closed: "hidden"
                  */}
              <svg
                class="hidden h-24 w-24"
                fill="none"
                viewBox="0 0 24 24"
                stroke-width="1.5"
                stroke="currentColor"
                aria-hidden="true"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  d="M6 18L18 6M6 6l12 12"
                />
              </svg>
            </button>
          </div>
          <div class="flex flex-1 items-center justify-start  md:justify-between">
            <div class="flex flex-shrink-0 items-center">
              <img
                class="block h-12 lg:h-16 xl:h-20 w-auto lg:hidden"
                src={require("../../img/logo-full.png")}
                alt="Your Company"
                href="/"
              />
              <img
                class="hidden h-8 lg:h-16 xl:h-20 w-auto lg:block drop-shadow-2xl"
                src={require("../../img/logo.png")}
                alt="Your Company"
                href="/"
              />
              <h1 className="hidden lg:block lg:text-2xl xl:text-3xl font-semibold text-white pl-4 drop-shadow-2xl">
                Otis Entertainment
              </h1>
            </div>
            <div class="hidden sm:ml-6 items-center lg:block">
              <div class="flex space-x-6">
                {/* Current: "bg-gray-900 text-white", Default: "text-gray-300 hover:bg-gray-700 hover:text-white" */}
                <a>{<PageButton href="/">Home</PageButton>}</a>
                <a className="relative group">
                  <Link
                    className={
                      path === "/solutions"
                        ? "bg-gray-900 text-primary-300 hover:text-primary-300 rounded-md px-5 py-4 lg:text-lg md:text-md font-medium transition-all ease-in duration-300"
                        : "text-gray-100 hover:bg-gray-700 hover:text-primary-300 rounded-md px-5 py-4 lg:text-lg md:text-md font-medium transition-all ease-in duration-300 group-hover:text-primary-300 group-hover:bg-gray-900 group-hover:animate-pulse"
                    }
                    to=""
                  >
                    Solutions
                  </Link>
                  <div className="hidden absolute z-40 group-hover:block left-[-150px] pt-10 xl:pt-12 transition group-hover:ease-in-out duration-700">
                    <div className="bg-white h-auto w-screen rounded-2xl shadow-xl border-8 border-t-0 border-gray-50 outline-none max-w-screen-sm px-4 pb-4 pt-4 gap-y-2">
                      <div className="grid grid-cols-4 w-auto justify-between gap-x-4 rounded-xl">
                        <div className="col-span-3 grid justify-evenly">
                          <div className="px-4 py-8 ">
                            <h2 className="text-base text-gray-400 font-semibold">
                              ARTISTS
                            </h2>
                            <h1 className="text-md font-bold ">
                              Boost your career
                            </h1>
                            <p className="text-sm">
                              Connect with new audiences and create lifelong
                              relationships with local venues.
                            </p>
                          </div>
                        </div>
                        <div className="col-span-1 grid justify-evenly">
                          <div className="flex items-center justify-center">
                            <Link
                              to="solutions/artists"
                              class="flex mx-auto lg:mx-0 justify-center py-4 xl:items-center space-x-4 bg-gray-200 rounded-full hover:bg-gray-300 focus:bg-gray-300 px-4"
                            >
                              <div>
                                <svg
                                  xmlns="http://www.w3.org/2000/svg"
                                  fill="none"
                                  viewBox="0 0 24 24"
                                  stroke-width="1.5"
                                  stroke="currentColor"
                                  class="w-9 h-9 text-primary-200"
                                >
                                  <path
                                    stroke-linecap="round"
                                    stroke-linejoin="round"
                                    d="M12.75 15l3-3m0 0l-3-3m3 3h-7.5M21 12a9 9 0 11-18 0 9 9 0 0118 0z"
                                  />
                                </svg>
                              </div>
                            </Link>
                          </div>
                        </div>
                      </div>

                      <div className="grid grid-cols-4 w-auto justify-between gap-x-4 rounded-xl">
                        <div className="col-span-3 grid justify-evenly">
                          <div className="px-4 py-8">
                            <h2 className="text-base text-gray-400 font-semibold">
                              VENUES
                            </h2>
                            <h1 className="text-md font-bold">
                              Venue management made easy
                            </h1>
                            <p className="text-sm">
                              Streamline your venue operations and experience
                              hassle-free management like never before.
                            </p>
                          </div>
                        </div>
                        <div className="col-span-1 grid justify-evenly">
                          <div className="flex items-center justify-center">
                            <Link
                              to="solutions/venues"
                              class="flex mx-auto lg:mx-0 justify-center py-4 xl:items-center space-x-4 bg-gray-200 rounded-full hover:bg-gray-300 focus:bg-gray-300 px-4"
                            >
                              <div>
                                <svg
                                  xmlns="http://www.w3.org/2000/svg"
                                  fill="none"
                                  viewBox="0 0 24 24"
                                  stroke-width="1.5"
                                  stroke="currentColor"
                                  class="w-9 h-9 text-primary-200"
                                >
                                  <path
                                    stroke-linecap="round"
                                    stroke-linejoin="round"
                                    d="M12.75 15l3-3m0 0l-3-3m3 3h-7.5M21 12a9 9 0 11-18 0 9 9 0 0118 0z"
                                  />
                                </svg>
                              </div>
                            </Link>
                          </div>
                        </div>
                      </div>

                      <div className="grid grid-cols-4 w-auto justify-between gap-x-4 rounded-xl">
                        <div className="col-span-3 grid justify-evenly">
                          <div className="px-4 py-8">
                            <h2 className="text-base text-gray-400 font-semibold">
                              INVESTORS
                            </h2>
                            <h1 className="text-md font-bold">
                              Investor opportunities
                            </h1>
                            <p className="text-sm">
                              Capitalize on the untapped potential of the artist
                              industry and unlock investment rewards.
                            </p>
                          </div>
                        </div>
                        <div className="col-span-1 grid justify-evenly">
                          <div className="flex items-center justify-center">
                            <Link
                              to="/solutions/investors"
                              class="flex mx-auto lg:mx-0 justify-center py-4 xl:items-center space-x-4 bg-gray-200 rounded-full hover:bg-gray-300 focus:bg-gray-300 px-4"
                            >
                              <div>
                                <svg
                                  xmlns="http://www.w3.org/2000/svg"
                                  fill="none"
                                  viewBox="0 0 24 24"
                                  stroke-width="1.5"
                                  stroke="currentColor"
                                  class="w-9 h-9 text-primary-200"
                                >
                                  <path
                                    stroke-linecap="round"
                                    stroke-linejoin="round"
                                    d="M12.75 15l3-3m0 0l-3-3m3 3h-7.5M21 12a9 9 0 11-18 0 9 9 0 0118 0z"
                                  />
                                </svg>
                              </div>
                            </Link>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </a>
                <a>{<PageButton href="/faqs">FAQs</PageButton>}</a>
                <a>{<PageButton href="/contact">Contact</PageButton>}</a>
                <a>{<PageButton href="/about">About</PageButton>}</a>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Mobile menu, show/hide based on menu state. */}
      <div
        id="mobile-menu"
        class={toggle ? "hidden" : "lg:hidden overscroll-none "}
      >
        <div class="space-y-1 px-2 pt-12 min-h-screen overscroll-none">
          <div className="rounded-md px-3 py-2 grid grid-rows-5 gap-y-5 text-base font-medium">
            {/* Current: "bg-gray-900 text-white", Default: "text-gray-300 hover:bg-gray-700 hover:text-white" */}

            <a>{<PageButton2 href="/">Home</PageButton2>}</a>
            <a>{<PageButton2 href="/solutions">Solutions</PageButton2>}</a>
            <a>{<PageButton2 href="/faqs">FAQs</PageButton2>}</a>
            <a>{<PageButton2 href="/contact">Contact</PageButton2>}</a>
            <a>{<PageButton2 href="/about">About</PageButton2>}</a>
          </div>
        </div>
      </div>
    </div>
  );
}

const Header = () => {

  const path = window.location.pathname;
  const [scrollY, setScrollY] = useState(0);
  const { scrollY: scrollYPosition } = useScroll();

  // Update scrollY value on scroll
  useEffect(() => {
    const updateScrollY = () => {
      setScrollY(scrollYPosition.get());
    };

    const unsubscribe = scrollYPosition.onChange(updateScrollY);

    return () => {
      unsubscribe();
    };
  }, [scrollYPosition]);

  // Define the transition values
  const transition = {
    duration: 0.4, // Adjust as desired
    ease: "easeInOut", // Adjust as desired
  };

  // Define the range of values for the scroll position
  const gradientOpacity = useTransform(scrollYPosition, [0, 100], [1, 0]);

  return path === "/" ? (
    <div className="bg-gradient-to-br from-primary-200 to-primary-100 h-24 z-40">
      <AnimatePresence>
        {scrollY > 100 ? (
          <motion.nav
            key="solid"
            className="absolute top-0 left-0 w-full p-6 grow-0 bg-gray-800"
            transition={transition}
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
          >
            <HeaderContent />
          </motion.nav>
        ) : (
          <motion.nav
            key="gradient"
            className="absolute top-0 left-0 w-full p-6 grow-0 bg-gradient-to-br from-primary-200 to-primary-100"
            transition={transition}
            initial={{ opacity: 1 }}
            animate={{ opacity: gradientOpacity }}
            exit={{ opacity: 1 }}
          >
            <HeaderContent />
          </motion.nav>
        )}
      </AnimatePresence>
    </div>
  ) : (
    <div className="w-full p-6 grow-0 bg-gray-800">
      <HeaderContent />
    </div>
  );
};
export default Header;

function PageButton({ href, children, ...props }) {
  const path = window.location.pathname;
  return (
    <a>
      <Link
        className={
          path === href
            ? "bg-gray-900 text-primary-300 hover:text-primary-300 rounded-md px-5 py-4 lg:text-lg md:text-md font-medium transition-all ease-in duration-300"
            : "text-gray-100 hover:bg-gray-700 hover:text-primary-300 rounded-md px-5 py-4 lg:text-lg md:text-md font-medium transition-all ease-in duration-300"
        }
        to={href}
      >
        {children}
      </Link>
    </a>
  );
}
