import React from "react";
import ReactDOM from "react-dom/client";
import {createBrowserRouter, Outlet, Routes, Route, RouterProvider } from "react-router-dom";
import "./index.css";
import Layout from "./pages/layout/layout"
import PopUp from "./pages/components/popup";
import ScrollToTop from "./pages/components/ScrollToTop";
import Root from "./pages/routes/root";
import reportWebVitals from "./reportWebVitals";
import ErrorPage from "./pages/routes/error-page";
import Faqs from "./pages/routes/faqs";
import Solutions from "./pages/routes/solutions";
import Contact from "./pages/routes/contact";
import About from "./pages/routes/about";
import PrivacyPolicy from "./pages/routes/privacy-policy";
import Licensing from "./pages/routes/licensing";
import Register from "./pages/routes/register";


const router = createBrowserRouter([
  {
    path: "/",
    element: <Root />,
    errorElement: <ErrorPage />,
  },
  {
    path: "solutions",
    element: <Solutions />,
  },
  {
    path: "faqs",
    element: <Faqs />,
  },
  {
    path: "about",
    element: <About />,
  },
  {
    path: "contact",
    element: <Contact />,
  },
  {
    path: "privacy-policy",
    element: <PrivacyPolicy />,
  },
  {
    path: "licensing",
    element: <Licensing />,
  },
  {
    path: "register",
    element: <Register />,
  },
  {
    path: "*",
    element: <ErrorPage />,
  },
]);


ReactDOM.createRoot(document.getElementById("root")).render(
  <React.StrictMode>
    <RouterProvider router={router}>
      <Layout>
        <Outlet />
      </Layout>
    </RouterProvider>
  </React.StrictMode>,
  document.getElementById("root")
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
