import React from "react";
import { Link } from "react-router-dom";

function Footer() {
  return (
    <div>
      <section class="py-10 bg-gray-100 sm:py-16 lg:py-20">
        <div class="max-w-5xl px-4 mx-auto sm:px-6 lg:px-8">
          <div class="text-center lg:flex lg:items-center lg:justify-between lg:text-left">
            <h2 class="text-4xl font-bold text-black lg:max-w-md">
              Hear the latest from <br /> Otis Entertainment
            </h2>
            <form action="#" method="POST" class="px-10 mt-8 lg:mt-0 lg:px-0">
              <div class="sm:flex sm:justify-center lg:justify-end">
                <div>
                  <label for="email" class="sr-only"></label>
                  <input
                    type="email"
                    name="email"
                    id="email"
                    placeholder="Enter email"
                    class="block w-full px-4 py-4 text-base text-black placeholder-gray-500 transition-all duration-200 border-gray-200 rounded-md sm:rounded-r-none caret-primary-300 focus:border-primary-300 focus:ring-1 focus:ring-primary-300"
                    required
                  />
                </div>
                <button
                  type="submit"
                  class="inline-flex items-center justify-center flex-shrink-0 w-full px-4 py-4 mt-4 font-semibold text-white transition-all duration-200 bg-primary-300 border border-transparent rounded-md sm:mt-0 sm:rounded-l-none sm:w-auto hover:bg-primary-400 focus:bg-primary-400"
                >
                  Subscribe
                </button>
              </div>
            </form>
          </div>
        </div>
      </section>

      <div className="flex flex-col bg-gray-800">
        <footer className="shadow p-5 md:px-6 md:py-8 mx-auto sm:mx-0">
          <div className="grid grid-cols-1 grid-rows-4 jusitfy-center">
            <div>
              <a href="/" target="_blank" className="mb-4 sm:mb-0">
                <div className="grid justify-center">
                  <img
                    src={require("../../img/logo.png")}
                    className=" mr-4 h-16 lg:h-20 xl:h-20 items-center"
                    alt="Flowbite Logo"
                  />
                </div>
              </a>
            </div>
            <div className="grid justify-center items-center">
              <span className="text-xl lg:text-2xl xl:text-3xl md:text-2xl font-semibold whitespace-nowrap text-white">
                Otis Entertainment
              </span>
            </div>
            <ul className="flex flex-wrap justify-center items-center sm:mb-0">
              {<FooterLink href="/about">About</FooterLink>}
              {<FooterLink href="/privacy-policy">Privacy Policy</FooterLink>}
              {<FooterLink href="/licensing">Licensing</FooterLink>}
              {<FooterLink href="/contact">Contact</FooterLink>}
            </ul>
            <div className="grid justify-center">
              <div class="pt-8 flex w-screen max-w-lg justify-around">
                {/* Facebook */}
                <a
                  href="#"
                  target="_blank"
                  class="text-gray-400 transition-colors duration-200 hover:text-gray-800"
                >
                  <svg
                    width="20"
                    height="20"
                    fill="currentColor"
                    class="text-xl transition-colors duration-200 hover:text-gray-800"
                    viewBox="0 0 1792 1792"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path d="M1343 12v264h-157q-86 0-116 36t-30 108v189h293l-39 296h-254v759h-306v-759h-255v-296h255v-218q0-186 104-288.5t277-102.5q147 0 228 12z"></path>
                  </svg>
                </a>
                {/* Linkedin */}
                <a
                  href="https://www.facebook.com/profile.php?id=100092511079706"
                  target="_blank"
                  class="text-gray-400 transition-colors duration-200 hover:text-gray-800"
                >
                  <svg
                    width="20"
                    height="20"
                    fill="currentColor"
                    class="text-xl transition-colors duration-200 hover:text-gray-800"
                    viewBox="0 0 1792 1792"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path d="M477 625v991h-330v-991h330zm21-306q1 73-50.5 122t-135.5 49h-2q-82 0-132-49t-50-122q0-74 51.5-122.5t134.5-48.5 133 48.5 51 122.5zm1166 729v568h-329v-530q0-105-40.5-164.5t-126.5-59.5q-63 0-105.5 34.5t-63.5 85.5q-11 30-11 81v553h-329q2-399 2-647t-1-296l-1-48h329v144h-2q20-32 41-56t56.5-52 87-43.5 114.5-15.5q171 0 275 113.5t104 332.5z"></path>
                  </svg>
                </a>
                {/* Instagram */}
                <a
                  href="https://www.instagram.com/otisentertainment/"
                  target="_blank"
                  class="text-gray-400 transition-colors duration-200 hover:text-gray-800"
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="20"
                    height="20"
                    fill="currentColor"
                    class="text-xl transition-colors duration-200 hover:text-gray-800"
                    viewBox="0 0 16 16"
                  >
                    <path d="M8 0C5.829 0 5.556.01 4.703.048 3.85.088 3.269.222 2.76.42a3.917 3.917 0 0 0-1.417.923A3.927 3.927 0 0 0 .42 2.76C.222 3.268.087 3.85.048 4.7.01 5.555 0 5.827 0 8.001c0 2.172.01 2.444.048 3.297.04.852.174 1.433.372 1.942.205.526.478.972.923 1.417.444.445.89.719 1.416.923.51.198 1.09.333 1.942.372C5.555 15.99 5.827 16 8 16s2.444-.01 3.298-.048c.851-.04 1.434-.174 1.943-.372a3.916 3.916 0 0 0 1.416-.923c.445-.445.718-.891.923-1.417.197-.509.332-1.09.372-1.942C15.99 10.445 16 10.173 16 8s-.01-2.445-.048-3.299c-.04-.851-.175-1.433-.372-1.941a3.926 3.926 0 0 0-.923-1.417A3.911 3.911 0 0 0 13.24.42c-.51-.198-1.092-.333-1.943-.372C10.443.01 10.172 0 7.998 0h.003zm-.717 1.442h.718c2.136 0 2.389.007 3.232.046.78.035 1.204.166 1.486.275.373.145.64.319.92.599.28.28.453.546.598.92.11.281.24.705.275 1.485.039.843.047 1.096.047 3.231s-.008 2.389-.047 3.232c-.035.78-.166 1.203-.275 1.485a2.47 2.47 0 0 1-.599.919c-.28.28-.546.453-.92.598-.28.11-.704.24-1.485.276-.843.038-1.096.047-3.232.047s-2.39-.009-3.233-.047c-.78-.036-1.203-.166-1.485-.276a2.478 2.478 0 0 1-.92-.598 2.48 2.48 0 0 1-.6-.92c-.109-.281-.24-.705-.275-1.485-.038-.843-.046-1.096-.046-3.233 0-2.136.008-2.388.046-3.231.036-.78.166-1.204.276-1.486.145-.373.319-.64.599-.92.28-.28.546-.453.92-.598.282-.11.705-.24 1.485-.276.738-.034 1.024-.044 2.515-.045v.002zm4.988 1.328a.96.96 0 1 0 0 1.92.96.96 0 0 0 0-1.92zm-4.27 1.122a4.109 4.109 0 1 0 0 8.217 4.109 4.109 0 0 0 0-8.217zm0 1.441a2.667 2.667 0 1 1 0 5.334 2.667 2.667 0 0 1 0-5.334z" />
                  </svg>
                </a>
                {/* TikTok */}
                <a
                  href=""
                  target="_blank"
                  class="text-gray-400 transition-colors duration-200 hover:text-gray-800"
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="20"
                    height="20"
                    fill="currentColor"
                    class="text-xl transition-colors duration-200 hover:text-gray-800"
                    viewBox="0 0 16 16"
                  >
                    <path d="M9 0h1.98c.144.715.54 1.617 1.235 2.512C12.895 3.389 13.797 4 15 4v2c-1.753 0-3.07-.814-4-1.829V11a5 5 0 1 1-5-5v2a3 3 0 1 0 3 3V0Z" />
                  </svg>
                </a>
              </div>
            </div>
          </div>
          <hr className="my-6 border-gray-200 sm:mx-auto dark:border-gray-700 lg:my-8" />
          <span className="block text-sm text-center text-gray-400">
             2023{" "}
            <a
              href={require("../../img/logo.png")}
              target="_blank"
              className="hover:underline"
            >
              Otis Entertainment
            </a>
            . All Rights Reserved.
          </span>
        </footer>
      </div>
    </div>
  );
}

export default Footer;

function FooterLink({ href, children }) {
  return (
    <li>
      <a>
        <Link
          to={href}
          className="mr-4 text-sm hover:underline md:mr-6 text-gray-400"
        >
          {children}
        </Link>
      </a>
    </li>
  );
}
