import React from "react";
import { motion } from "framer-motion";

function PopUp() {
  return (
    <motion.div initial={{ opacity: 0.8 }}
    whileInView={{ opacity: 1 }}
    transition={{ duration: 1 }}
    class="w-full px-4 text-center text-xs sm:text-md font-bold text-white bg-gray-700">
      <p class="py-2 animate pulse">
        Want to give Artists a lifeline? Express your interest now!
      </p>
    </motion.div>
  );
}

export default PopUp;
