import { useRef } from "react";
import { useInView, cubicBezier, easeOut, useTransform } from "framer-motion";


export  function FadeSection({ children }) {
  const ref = useRef(null);
  const isInView = useInView(ref, { once: true });

  return (
    <section ref={ref}>
      <span
        style={{
          transform: isInView ? "none" : "translateX(-200px)",
          opacity: isInView ? 1 : 0,
          transition: " all 1.75s cubic-bezier(0.17, 0.55, 0.55, 1) 0.75s"
        }}
      >
        {children}
      </span>
    </section>
  );
}

export function FadeHeader(x) {
  const y = useTransform(x, [0, 1], [0, 2], { ease: easeOut })

const z = useTransform(
  x,
  [0, 1],
  [0, 2],
  { ease: cubicBezier(0.17, 0.67, 0.83, 0.67) }
)
}