import { Link } from "react-router-dom";
import React, { useState } from "react";
import { useForm } from "react-hook-form";
import PopUp from "../components/popup";
import Layout from "../layout/layout"

function Contact() {
  const { register, handleSubmit } = useForm();
  const onSubmit = (data) => console.log(data);

  return (
    <Layout page="Contact">
          <div className="w-screen bg-white py-24 px-6">
            <div className="mx-auto max-w-2xl text-center">
              <h2 className="text-3xl font-bold tracking-tight text-gray-900 sm:text-4xl">
                Contact the team
              </h2>
              <p className="mt-2 text-lg leading-8 text-gray-600">
                Submit a request to the Otis Entertainment team and receive an
                email back.
              </p>
            </div>
            <form
              className="mx-auto mt-16 max-w-xl sm:mt-20"
              onSubmit={handleSubmit(onSubmit)}
            >
              <div className="grid grid-cols-1 gap-y-6 gap-x-8 sm:grid-cols-2">
                <div>
                  <label
                    htmlFor="first-name"
                    className="block text-sm font-semibold leading-6 text-gray-900"
                  >
                    First name
                  </label>
                  <div className="mt-2.5">
                    <input
                      type="string"
                      placeholder="John"
                      {...register("firstName", {
                        required: true,
                        maxLength: 20,
                      })}
                      className="block w-full rounded-md border-0 py-2 px-3.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-primary-300 hover:ring-primary-300 sm:text-sm sm:leading-6"
                    />
                  </div>
                </div>
                <div>
                  <label
                    htmlFor="last-name"
                    className="block text-sm font-semibold leading-6 text-gray-900"
                  >
                    Last name
                  </label>
                  <div className="mt-2.5">
                    <input
                      type="string"
                      placeholder="Smith"
                      {...register("lastName", { required: true, maxLength: 20 })}
                      className="block w-full rounded-md border-0 py-2 px-3.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-primary-300 hover:ring-primary-300 sm:text-sm sm:leading-6"
                    />
                  </div>
                </div>
                <div className="sm:col-span-2">
                  <label
                    htmlFor="email"
                    className="block text-sm font-semibold leading-6 text-gray-900"
                  >
                    Email
                  </label>
                  <div className="mt-2.5">
                    <input
                      type="text"
                      className="block w-full rounded-md border-0 py-2 px-3.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-primary-300 hover:ring-primary-300 sm:text-sm sm:leading-6"
                      placeholder="johnsmith@mail.com"
                      {...register("Email", {
                        required: true,
                        pattern: /^\S+@\S+$/i,
                      })}
                    />
                  </div>
                </div>
                <div className="sm:col-span-2">
                  <label
                    htmlFor="message"
                    className="block text-sm font-semibold leading-6 text-gray-900"
                  >
                    Message
                  </label>
                  <div className="mt-2.5">
                    <textarea
                      name="message"
                      id="message"
                      rows="4"
                      className="block w-full rounded-md border-0 py-2 px-3.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-primary-300 hover:ring-primary-300 sm:text-sm sm:leading-6"
                    ></textarea>
                  </div>
                </div>
                <div className="flex gap-x-4 sm:col-span-2">
                  {/* Enabled: "bg-primary-300", Not Enabled: "bg-gray-200" */}
                  <div className="mb-3">
                    <div className="relative inline-block w-10 mr-2 align-middle select-none">
                      <input
                        type="checkbox"
                        name="toggle"
                        id="Orange"
                        className="checked:bg-primary-300 checked:hover:bg-primary-400 focus:outline-secondary-300 checked:focus:bg-primary-200 outline-none focus:outline-none right-4 checked:right-0 duration-200 ease-in absolute block w-6 h-6 rounded-full bg-white border-4 appearance-none cursor-pointer"
                      />
                      <label
                        htmlFor="Orange"
                        className="block h-6 overflow-hidden bg-gray-300 rounded-full cursor-pointer"
                      ></label>
                    </div>
                  </div>
                  <label
                    className="text-sm leading-6 text-gray-600"
                    id="switch-1-label"
                  >
                    By selecting this, you agree to our {}
                    <a href="#" className="font-semibold text-primary-300">
                      privacy&nbsp;policy
                    </a>
                    .
                  </label>
                </div>
              </div>
              <div className="mt-10">
                <input
                  type="submit"
                  className="block w-full rounded-md bg-primary-300 px-3.5 py-2.5 text-center text-sm font-semibold text-white shadow-sm hover:bg-primary-400 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-primary-300"
                ></input>
              </div>
            </form>
          </div>
    </Layout>
  );
}

export default Contact;
