import { Link } from "react-router-dom";
import React, { useState } from "react";
import PopUp from "../components/popup";
import Layout from "../layout/layout";

function Faqs() {
  return (
    <Layout page="FAQs">
          <section class="py-10 bg-gray-50 sm:py-16 lg:py-24">
            <div class="px-4 mx-auto sm:px-6 lg:px-8 max-w-7xl">
              <div class="max-w-2xl mx-auto text-center pb-20">
                <h2 class="text-3xl font-bold leading-tight text-black sm:text-4xl lg:text-5xl">
                  Frequently Asked Questions
                </h2>
                <p class="max-w-xl mx-auto mt-4 text-base leading-relaxed text-gray-600">
                  At Otis Entertainment, we're dedicated to providing the best
                  possible experience for all our users. If you have any
                  additional questions or concerns, please don't <br></br>
                  hesitate to reach out to our support team.
                </p>
              </div>

              <div class="max-w-3xl mx-auto mt-8 space-y-4 md:mt-8">
                <h3 class="pt-8 pb-8 max-w-2xl mx-auto text-center text-2xl font-bold leading-tight text-black sm:text-3xl lg:text-4xl">
                  For Investors
                </h3>
                {Faq(
                  "What is Otis Entertainment?",
                  "Otis Entertainment is a platform that connects artists and venues, helping artists find new talent and venues reach wider audiences. Our goal is to make it easy for artists and venues to find each other, collaborate and build long-lasting relationships."
                )}
                {Faq(
                  "What problem does Otis Entertainment solve?",
                  "Otis Entertainment solves the problem of artists and venues struggling to find each other and build relationships. Our platform makes it easy for artists and venues to discover and connect with each other, so they can focus on creating great events."
                )}
                {Faq(
                  "How does Otis Entertainment make money?",
                  "Otis Entertainment generates revenue through commissions on booking transactions and from optional premium services."
                )}
                {Faq(
                  "What is the current financial state of Otis Entertainment?",
                  "Please contact us for information on our current financial state."
                )}
                {Faq(
                  "What is the potential return on investment for shareholders?",
                  "The potential return on investment for shareholders will depend on the success of the platform and can be affected by a number of factors including competition, market trends, and overall financial performance."
                )}
                {Faq(
                  "What is the long-term vision for Otis Entertainment?",
                  "Our long-term vision is to become the go-to platform for artists and venues to connect, collaborate, and build long-lasting relationships. We aim to continuously improve the platform and expand our offerings to better serve our community."
                )}
                {Faq(
                  "What is the process for investing in Otis Entertainment?",
                  "You can invest in our platform by visiting our crowd-funding platform hosted by Swarmer. For larger investments, please contact us for more information."
                )}
              </div>
              <div class="max-w-3xl mx-auto mt-8 space-y-4 md:mt-8">
                <h3 class="pt-8 pb-6 max-w-2xl mx-auto text-center text-2xl font-bold leading-tight text-black sm:text-3xl lg:text-4xl">
                  For Artists
                </h3>
                {Faq(
                  "How does Otis Entertainment help artists find new talent?",
                  "Otis Entertainment provides a platform for artists to showcase their work, connect with venues, and discover new talent. Our platform makes it easy for artists to find opportunities, collaborate, and build their careers."
                )}
                {Faq(
                  "How does Otis Entertainment help artists reach wider audiences?",
                  "Otis Entertainment helps artists reach wider audiences by providing a platform for them to showcase their work and connect with venues. This allows artists to reach new audiences and build their careers."
                )}
                {Faq(
                  "What is the process for signing up for Otis Entertainment?",
                  "To sign up for Otis Entertainment, simply create a profile and start showcasing your work. Our platform makes it easy for artists to connect with venues, discover new opportunities, and build their careers."
                )}
                {Faq(
                  "What is the cost for artists to use Otis Entertainment?",
                  "Otis Entertainment is free for artists to use, but we do offer optional premium services for a fee."
                )}
                {Faq(
                  "How secure is my personal information on Otis Entertainment?",
                  "Otis Entertainment takes the security of our users' personal information very seriously. We use industry-standard security measures to protect our users' data, and we never share personal information without consent."
                )}
                {Faq(
                  "How does Otis Entertainment handle disputes between artists and venues?",
                  "Otis Entertainment has a dispute resolution process in place to help resolve any disputes that may arise between artists and venues. Our goal is to provide a fair and neutral platform for artists and venues to connect and collaborate."
                )}
                {Faq(
                  "Can I set my own price for my services on Otis Entertainment?",
                  "Yes, artists can choose which event requests they wish to apply for on the Otis platform. All event requests are screened for fair and equal trade purposes."
                )}
              </div>
              <div class="max-w-3xl mx-auto mt-8 space-y-4 md:mt-16">
                <h3 class="pt-8 pb-6 max-w-2xl mx-auto text-center text-2xl font-bold leading-tight text-black sm:text-3xl lg:text-4xl">
                  For Artists
                </h3>

                {Faq(
                  "How does Otis Entertainment help venues find new talent?",
                  "Otis Entertainment provides a platform for venues to discover and connect with artists, helping them find new talent for their events. Our platform makes it easy for venues to find artists, showcase their venue, and build relationships with artists."
                )}
                {Faq(
                  "How does Otis Entertainment help venues reach wider audiences?",
                  "Otis Entertainment helps venues reach wider audiences through our marketing and promotional efforts. We also allow venues to showcase their events and offerings on our platform, helping to attract new audiences."
                )}
                {Faq(
                  "What is the process for signing up for Otis Entertainment?",
                  "The process for signing up for Otis Entertainment is simple and straightforward. You can visit our website and click on the 'Sign Up' button, then fill out the necessary information to create a venue profile. Our team will review your information and approve your profile once all the required information has been received."
                )}
                {Faq(
                  "What is the cost for venues to use Otis Entertainment?",
                  "There is a commission fee for each booking made through Otis Entertainment. A partial amount of the commission fee goes directly towards the Lifeline Donation. The exact fee will vary based on the specific details of each booking."
                )}
                {Faq(
                  "How secure is my personal information on Otis Entertainment?",
                  "Otis Entertainment takes the security of your personal information very seriously. We use industry-standard security measures to protect your information, and we do not share your information with third parties."
                )}
                {Faq(
                  "How does Otis Entertainment handle disputes between artists and venues?",
                  "Otis Entertainment has a dispute resolution process in place to handle any disputes between venues and artists. Our team will review the dispute and work with both parties to reach a fair resolution."
                )}
                {Faq(
                  "Can I set my own price for my services on Otis Entertainment?",
                  "Yes, venues are able to set their own prices for their services on Otis Entertainment. However, our platform also provides pricing suggestions based on industry standards to help ensure that your prices are fair, equal and competitive."
                )}
              </div>
              <p class="text-center text-gray-600 textbase mt-9">
                Didnt find the answer you are looking for?{" "}
                <a
                  href="/contact"
                  title=""
                  class="font-medium text-primary-300 transition-all duration-200 hover:text-primary-400 focus:text-primary-300 hover:underline"
                >
                  Contact us
                </a>
              </p>
            </div>
          </section>
    </Layout>
  );
}

export default Faqs;

function Faq(title, description) {
  const [cardtoggle, setCardToggle] = useState(false);

  const cardToggler = () => {
    cardtoggle ? setCardToggle(false) : setCardToggle(true);
  };

  return (
    <div class="transition-all duration-200 bg-white border rounded-lg border-gray-200 shadow-lg cursor-pointer hover:bg-gray-50">
      <button
        type="button"
        onClick={cardToggler}
        class="flex items-center justify-between w-full px-4 py-5 sm:p-6 "
      >
        <span
          class="flex text-lg font-semibold text-black "
          textBreakStrategy="simple"
        >
          {" "}
          {title}{" "}
        </span>
        <svg
          class={
            cardtoggle
              ? "w-6 h-6 text-gray-400 rotate-180"
              : "w-6 h-6 text-gray-400"
          }
          xmlns="http://www.w3.org/2000/svg"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="2"
            d="M19 9l-7 7-7-7"
          />
        </svg>
      </button>
      <div
        class={
          cardtoggle
            ? "px-4 pb-5 sm:px-6 sm:pb-6"
            : "hidden px-4 pb-5 sm:px-6 sm:pb-6"
        }
      >
        <p>{description}</p>
      </div>
    </div>
  );
}
