import React from "react";
import { Link } from "react-router-dom";
import { useRouteError } from "react-router-dom";

export default function ErrorPage() {
  const error = useRouteError();
  console.error(error);

  return (
    <html>
      <head>
        <title>404 - Otis Entertainment</title>
      </head>
      <div id="error-page" className="h-full">
        <main class="grid min-h-full place-items-center bg-white py-24 px-6 sm:py-32 lg:px-8">
          <div class="text-center">
            <p class="text-base font-semibold text-primary-300">404</p>
            <h1 class="mt-4 text-3xl font-bold tracking-tight text-gray-900 sm:text-5xl">
              Page not found
            </h1>
            <p class="mt-6 text-base leading-7 text-gray-600">
              Sorry, we couldnt find the page youre looking for.
            </p>
            <div class="mt-10 flex items-center justify-center gap-x-6">
              <a>
                <Link
                  to="/"
                  class="rounded-md bg-primary-300 px-3.5 py-2.5 text-sm font-semibold text-white shadow-sm hover:bg-primary-400 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                >
                  Go back home
                </Link>
              </a>
              <a>
                <Link to="/contact" class="text-sm font-semibold text-gray-900">
                  Contact support <span aria-hidden="true">&rarr;</span>
                </Link>
              </a>
            </div>
          </div>
        </main>
      </div>
    </html>
  );
}
