import React from "react";
import { Link } from "react-router-dom";
import insertimage from "../../img/insert-image.png";
import PopUp from "../components/popup";
import Layout from "../layout/layout";

function About() {
  return (
    <Layout page="About Us">
      <section class="py-10 bg-white sm:py-16 lg:py-24">
        <div class="max-w-5xl px-4 mx-auto sm:px-6 lg:px-8">
          <div class="mx-auto text-left md:max-w-lg lg:max-w-2xl md:text-center">
            <h2 class="text-3xl font-bold leading-tight text-black sm:text-4xl lg:text-5xl lg:leading-tight">
              Bridging the gap between venues and artists.
            </h2>
          </div>

          <div class="grid grid-cols-1 mt-8 md:mt-20 gap-y-6 md:grid-cols-2 gap-x-10">
            <div>
              <img
                class="w-full mx-auto sm:max-w-xs"
                src={insertimage}
                alt=""
              />
            </div>

            <div>
              <p class="mt-4 text-lg text-gray-700">
                Otis Entertainment is a dynamic and innovative entertainment
                company, founded on a passion for music and a vision to create
                unique and memorable experiences for audiences around the world.
                Our team is led by Stephen Scaife and Matt Davey, two industry
                veterans who bring a wealth of experience and expertise to the
                company.
              </p>

              <h3 class="mt-8 text-lg font-semibold text-gray-900">
                A bit about our Directors.
              </h3>
              <p class="mt-4 text-lg text-gray-700">
                Stephen is the creative mastermind behind Otis Entertainment,
                originating from his farm house during COVID-19 and is driven by
                a commitment to giving and supporting Lifeline through donations
                from the company's revenue. Matt, on the other hand, brings a
                wealth of experience in event management, marketing, and
                operations. He has been in the entertainment industry for over
                20 years and has worked with some of the biggest names in the
                business.
              </p>
            </div>
          </div>
        </div>
      </section>

      <section class="py-10 bg-gray-50 sm:py-16 lg:py-24">
        <div class="px-4 mx-auto sm:px-6 lg:px-8 max-w-7xl">
          <div class="max-w-2xl mx-auto text-center">
            <h2 class="text-3xl font-bold leading-tight text-black sm:text-4xl lg:text-5xl">
              Our Team
            </h2>
          </div>
          <div class="flex justify-between mt-8 text-center sm:mt-16 lg:mt-20 lg:grid-cols-8 gap-x-0">

            <div class="grid grid-row-3 content-around">
              <img
                class="w-auto h-fit mx-auto rounded-lg"
                src={insertimage}
                alt=""
              />
              <p class="mt-8 text-lg font-semibold leading-tight text-black">
                Stephen Scaife
              </p>
              <p class="mt-1 text-base leading-tight text-gray-600">Founder</p>
            </div>


            <div class="grid grid-row-3 content-around">
              <img
                class="w-auto h-fit object-cover mx-auto rounded-lg"
                src={insertimage}
                alt=""
              />
              <p class="mt-8 pt-4 text-lg font-semibold leading-tight text-black">
                Matt Davey
              </p>
              <p class="mt-1 text-base pt leading-tight text-gray-600">
                Co Founder
              </p>
            </div>

            <div>
              <img
                class="w-auto h-fit object-cover mx-auto rounded-lg"
                src={insertimage}
                alt=""
              />
              <p class="mt-8 text-lg pt-8 font-semibold leading-tight text-black">
                Sam Tape
              </p>
              <p class="mt-1 text-base leading-tight text-gray-600">
                Systems Coordinator
              </p>
            </div>

            <div>
              <img
                class="w-auto h-fit object-cover mx-auto rounded-lg"
                src={insertimage}
                alt=""
              />
              <p class="mt-8 text-lg pt-8 font-semibold leading-tight text-black">
                Georgie Reuther
              </p>
              <p class="mt-1 text-base leading-tight text-gray-600">
                Marketing Coordinator
              </p>
            </div>
          </div>
        </div>
      </section>
    </Layout>
  );
}

export default About;
